import { Routes } from '@angular/router';

import { ProfilRoleListComponent } from "./profilRole.list/profilRole.list.component";

export const ProfilRoleRoutes: Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{ path: 'list', component: ProfilRoleListComponent }]
}];



