"use strict";

import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { fadeInAnimation } from "../../../core/route-animation/route.animation";
import { ProfilRole } from "../metier/profilRole.metier";
import { BaseComponent } from "../../../core/component/base-component";
import { ProfilRoleService } from "../service/profilRole.service";
import { PageTitleService } from "../../../core/page-title/page-title.service";
import { Profil } from '../../profil/metier/profil.metier';
import { ProfilService } from '../../profil/service/profil.service';
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: 'ms-profilRole-list',
  templateUrl: './profilRole.list.component.html',
  styleUrls: ['./profilRole.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class ProfilRoleListComponent extends BaseComponent implements OnInit {

  private profilRoleAffecteList: Array<ProfilRole> = [];
  private profilRoleNonAffecteList: Array<ProfilRole> = [];
  private profil_id: number;


  constructor(private profilRoleService: ProfilRoleService,
    private profilService: ProfilService,
    private pageTitleService: PageTitleService,
    private route: ActivatedRoute) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AFFECTATION DES PROFILS");
    this.loadProfil();
  }

  private loadProfil() {
    this.route.queryParams.subscribe(params => {
      this.profil_id = params['profil_id'];
      this.profilService.getProfil(this.profil_id)
        .subscribe(profil => {
          this.loadProfilRoleAffecteList(profil);
          this.loadProfilRoleNonAffecteList(profil);
        }, error => {
          this.showError(error.status, error.json().message);
        });
    }, error => {
      this.showError(error.status, error.json().message);
    })
  }

  private loadProfilRoleAffecteList(profil: Profil) {
    this.profilRoleService.getRoleAffecte(profil)
      .subscribe(profilRoleAffecteList => {
        this.profilRoleAffecteList = profilRoleAffecteList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }


  private loadProfilRoleNonAffecteList(profil: Profil) {
    this.profilRoleService.getRoleNonAffecte(profil)
      .subscribe(profilRoleNonAffecteList => {
        this.profilRoleNonAffecteList = profilRoleNonAffecteList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  saveProfilRole() {
    let profilRoleList: Array<ProfilRole>;

    //delete
    profilRoleList = [];
    for (let profilRole of this.profilRoleNonAffecteList) {
      if (profilRole.id != null) {
        profilRoleList.push(profilRole);
      }
    }

    if (profilRoleList.length > 0) {
      this.profilRoleService.deleteProfilRoleList(profilRoleList)
        .subscribe(() => {
          this.showInfo("Suppression effectué avec succès");
        }, error => this.showError(error.status, error.json().message));
    }

    //save
    profilRoleList = [];
    for (let profilRole of this.profilRoleAffecteList) {
      if (profilRole.id === null) {
        profilRoleList.push(profilRole);
      }
    }

    if (profilRoleList.length > 0) {
      this.profilRoleService.saveProfilRoleList(profilRoleList)
        .subscribe(() => {
          this.showInfo("Enregistrement effectué avec succès");
        }, error => this.showError(error.status, error.json().message));
    }

    this.router.navigate(["/admin/profil/list/"]);
  }

  affecterRole(profilRole: ProfilRole, index: number) {
    this.profilRoleAffecteList.push(profilRole);
    this.profilRoleNonAffecteList.splice(index, 1);
  }

  retirerRole(profilRole: ProfilRole, index: number) {
    this.profilRoleNonAffecteList.push(profilRole);
    this.profilRoleAffecteList.splice(index, 1);
  }

  private cancel() {
    this.router.navigate(["/admin/profil/edit/" + this.profil_id]);
  }

}






