import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {ProfilRole} from "../metier/profilRole.metier";
import {SettingsService} from "../../../core/settings/settings.service";
import {AuthHttpService} from "../../../auth/auth-http.service";
import {Profil} from "../../profil/metier/profil.metier";

@Injectable()
export class ProfilRoleService {

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService) {
  }

  saveProfilRoleList(profilRoleList: Array<ProfilRole>): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/profilRole/saveList`, JSON.stringify(profilRoleList));
  }

  deleteProfilRoleList(profilRoleList: Array<ProfilRole>): Observable<Response> {
    return this.authHttp.post(this.settings.baseUrl + `/profilRole/deleteList`, JSON.stringify(profilRoleList));
  }

  getRoleAffecte(profil: Profil): Observable<Array<ProfilRole>> {
    return this.authHttp.post(this.settings.baseUrl + `/profilRole/getProfilRoleAffecte`, JSON.stringify(profil))
      .map((res: Response) => res.json());
  }

  getRoleNonAffecte(profil: Profil): Observable<Array<ProfilRole>> {
    return this.authHttp.post(this.settings.baseUrl + `/profilRole/getProfilRoleNonAffecte`, JSON.stringify(profil))
      .map((res: Response) => res.json());
  }
}



