import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {ProfilRoleRoutes} from './profilRole.routing';
import {ProfilRoleService} from "./service/profilRole.service";
import {ProfilRoleListComponent} from "./profilRole.list/profilRole.list.component";
import {DataTableModule} from "angular2-datatable";
import { DragulaModule } from 'ng2-dragula/ng2-dragula';
import { RoleService } from '../role/service/role.service';
import { ProfilService } from '../profil/service/profil.service';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    DragulaModule,
    WizardModule,
    RouterModule.forChild(ProfilRoleRoutes)
  ],
  declarations: [
    ProfilRoleListComponent,
  ],
  providers: [
    ProfilRoleService,
    RoleService,
    ProfilService
  ],
})

export class ProfilRoleModule {
}



